<template>
  <div class="Neibulistitem nblist">
    <ul v-for = "item in listItem" :key = "item.id">
        <li>
            <router-link :to="{ name:'Neibujiejue', params: {id: item.id}  }">
              <div>
                  <div class = "imgBox">
                    <img class = "qunzhongpic" src = "../assets/img/zhengzaizhixing.png">
                  </div>
                  <div class = "qunzhonginfo">
                    <div>{{item.name}}<span class = "qunzhongphone">{{item.time}}</span></div>
                    <div>{{item.address}}</div>
                    <div class = "zhixingInfo">{{item.zhixingInfo}}</div>
                  </div>
                  
              </div>
            </router-link>
        </li>
       
    </ul>

    
  </div>
</template>

<script>
export default {
  name: 'Neibulistitem',
  data () {
    return {
      msg: '',
      listItem:[
            {
              id:0,
              name:"张三",
              time:"09:20:03",
              address:"山西省太原市尖草坪区张家沟村",
              zhixingInfo:"有两男子不知道什么原因，正在打架，其中一",
 
            },
            {
              id:1,
              name:"李四",
              time:"08:20:03",
              address:"山西省太原市尖草坪区张家沟村",
              zhixingInfo:"放得开福建省的防守打法了短时间就发大水会计法",
 
            } 
       ]
    }
  },
  methods: {
       
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.nblist li{
    background:#fff;
    margin-bottom:0.2rem;
    overflow:hidden;
    
}
.nblist>ul>li>a>div{
  overflow:hidden;
}
.nblist>ul>li>a{
  display:block;
  padding:0.4rem 0.4rem;
}
.nblist li:last-child{
    border:0;
}
.zhixingInfo{
  overflow: hidden;
  text-overflow:ellipsis;
  white-space: nowrap;
  color:#888;
}
.imgBox{
  width:20%;
  float:left;
  margin-top: 0.25rem;
}
.qunzhonginfo{
  width:80%;
  float:left;
}
.qunzhongphone{
    margin-left:0.6rem;
 
}
.qunzhongpic{
    width:1.64rem;
    margin-right:  0.3rem;

}
.removequnzhong{
    width:0.6rem;
    float:right;
    margin-top: 0.5rem;
}
.qunzhonginfo{
    float:left;
    line-height: 25px;
}
input.file 
        {
            position: relative;
            -moz-opacity:0 ;
            filter:alpha(opacity: 0);
            opacity: 0;
            z-index: 2;
        } 
        .wrapper{
            color: #fff;
            background-color: #31b0d5;
            border-color: #269abc;
            margin-top: 5px;
            margin-bottom: 5px;
            display: inline-block;
            padding: 6px 12px;
            margin-bottom: 0;
            font-size: 14px;
            font-weight: 400;
            line-height: 1.42857143;
            text-align: center;
            white-space: nowrap;
            vertical-align: middle;
            -ms-touch-action: manipulation;
            touch-action: manipulation;
            cursor: pointer;
            -webkit-user-select: none;
            -moz-user-select: none;
            -ms-user-select: none;
            user-select: none;
            background-image: none;
            border: 1px solid transparent;
            border-radius: 4px;
        }
</style>



