}
.Qunzhong li:nth-child(2) .qzPic,.Qunzhong li:nth-child(4) .qzPic,.Qunzhong li:nth-child(9) .qzPic,.Qunzhong li:nth-child(14) .qzPic,.Qunzhong li:nth-child(10) .qzPic{
