}
.Qunzhong li:nth-child(3) .qzPic,.Qunzhong li:nth-child(5) .qzPic,.Qunzhong li:nth-child(7) .qzPic,.Qunzhong li:nth-child(15) .qzPic,.Qunzhong li:nth-child(11) .qzPic,.Qunzhong li:nth-child(17) .qzPic{
