<template>
  <div class="zhengzaizhixing">
    <ul v-for = "item in listItem" :key = "item.id">
        <li>
            <router-link :to="{ name:'Jingqingpanduan', params: {id: item.id}  }">
              <div>
                  <div class = "imgBox">
                    <img class = "qunzhongpic" src = "../assets/img/zhengzaizhixing.png">
                  </div>
                  <div class = "qunzhonginfo">
                    <div>{{item.name}}<span class = "qunzhongphone">{{item.time}}</span></div>
                    <div>{{item.address}}</div>
                    <div class = "zhixingInfo">{{item.zhixingInfo}}</div>
                  </div>
                  
              </div>
            </router-link>
            
        </li>
       
    </ul>
   
  </div>
</template>

<script>
export default {
  name: 'Zhengzaizhixing',
  data () {
    return {
      msg: '',
      listItem:[
            {
              id:0,
              name:"张三",
              time:"09:20:03",
              address:"山西省太原市尖草坪区张家沟村",
              zhixingInfo:"有两男子不知道什么原因，正在打架，其中一",
 
            },
            {
              id:1,
              name:"李四",
              time:"08:20:03",
              address:"山西省太原市尖草坪区张家沟村",
              zhixingInfo:"放得开福建省的防守打法了短时间就发大水会计法",
 
            } 
       ]
    }
  },

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.zhengzaizhixing li{
    background:#fff;
    margin-bottom:0.2rem;
    overflow:hidden;
    
}
.zhengzaizhixing>ul>li>a>div{
  overflow:hidden;
}
.zhengzaizhixing>ul>li>a{
  display:block;
  padding:0.4rem 0.4rem;
}
.zhengzaizhixing li:last-child{
    border:0;
}
.zhixingInfo{
  overflow: hidden;
  text-overflow:ellipsis;
  white-space: nowrap;
  color:#888;
}
.imgBox{
  width:20%;
  float:left;
  margin-top: 0.25rem;
}
.qunzhonginfo{
  width:80%;
  float:left;
}
.qunzhongphone{
    margin-left:0.6rem;
 
}
.qunzhongpic{
    width:1.64rem;
    margin-right:  0.3rem;

}
.removequnzhong{
    width:0.6rem;
    float:right;
    margin-top: 0.5rem;
}
.qunzhonginfo{
    float:left;
    line-height: 25px;
}
</style>



