<template>
  <div class="Woyaobaojing">
    <div class="tjbjContain">
        <div class="formBox">
            <form action="">
                <div class="formgroup">
                    <label for="">姓名</label>
                    <input type="text" v-model="username" readonly="readonly"/>
                </div>
                <div class="formgroup">
                    <label for="">联系电话</label>
                    <input type="text" readonly="readonly" v-model="userPhone"/>
                </div>
				<div class="formgroup">
                    <label for="">所属片区</label>
					<input type="text" readonly="readonly" v-model="userArea"/>
                    <!-- <Select v-model="model1">
						<Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
					</Select> -->
                </div>
                <div class="formgroup">
                    <label for="">发生时间</label>
                    <input readonly class="fashengtime" v-model="alarmTime"  type="text" id="date4" placeholder="YYYY-MM-DD hh:mm:ss">
                    
                </div>
                <div class="formgroup">
                    <label for="">事件地点</label>
                    <input type="text" v-model="alarmAddress" placeholder = "请输入详细地址"/>
                </div>
                <div class="formgroup">
                    <label for="">报警内容</label>
                    <img @click = "showToggle" class = "camerapic" src="../assets/img/camera.png" alt="">
                </div>
                <div class="formgroup">
                    <label for=""></label>
					<textarea class = "baojinginfo"  v-model="alarmInfo" contenteditable="true" tabindex="0">请输入报警内容</textarea>
                    <!-- <input type="text"  placeholder = "请输入报警内容"/> -->
                </div>
			
            </form>
        </div>
        <div class="submitBox">
            <input class = "submitBaojing" @click = "callPolice()" type="button" value = "警情上报"/>
        </div>
    </div>
	<div class = "mask" v-show = "isShow" @click = "showMask">
		<ul class = "maskInfo" @click = "zuzhiShijian">
			<li @click = 'getMedia'>
				<router-link to="">
					<img src = "../assets/img/photo.png">
					<div>照片</div>
				</router-link>
			</li>
			<li>
				<router-link to="">
					<img src = "../assets/img/shipin.png">
					<div>视频</div>
				</router-link>	
			</li>
			<li>
				<router-link to="">
					<img src = "../assets/img/luyin.png">
					<div>录音</div>
				</router-link>	
			</li>
		</ul>
	</div>
  </div>
  
</template>

<script>
import selectDate from '../assets/js/selectDate'

export default {
  name: 'Woyaobaojing',
  data () {
    return {
	  isShow:false,
	  username:"匿名",
	  userPhone:"13245457878",
	  userArea:"钢东社区",
	  alarmTime:"15",
	  alarmAddress:"请输入详细地址",
	  alarmInfo:"请输入报警内容",
	  
	  cityList: [
			{
				value: 'New York',
				label: 'New York'
			},
			{
				value: 'London',
				label: 'London'
			},
		
		],
		model1: ''
	  
    }
  },
  mounted: function () {
       let h = window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight;
    
	   console.log(h);
	   $(".mask").css("height",h+'px');
       var marginTopNum = h-$(".formBox").height()-$(".submitBox").height();
		console.log(marginTopNum);
        marginTopNum = marginTopNum/100+2;
        $(".submitBox").css("marginTop",marginTopNum+'rem');
        //时间处理部分
			
		new rolldate.Date({
			el:'#date4',
			format:'YYYY-MM-DD hh:mm:ss',
			beginYear:2000,
			endYear:2100
			})
		
		
		function add(m){
		    return m<10?'0'+m:m
		}
		function fortime(date){
		    var y = date.getFullYear();
		    var m = date.getMonth()+1;
		    var d = date.getDate();
		    var h = date.getHours();
		    var mm = date.getMinutes();
		    var s = date.getSeconds();
		    return(y+'-'+add(m)+'-'+add(d)+' '+add(h)+':'+add(mm)+':'+add(s));
		}
		
		var date = new Date(new Date().getTime() + (1000 * 60 * 60 * 72));
		var date_str = fortime(date);
	
		$(".fashengtime").attr("placeholder",date_str);


            
  },
  methods:{
	  showToggle:function(){
		  this.isShow = true;

	  },
	  showMask:function(event){
		  this.isShow = false;
	  },
	  zuzhiShijian:function(e){
		  e.stopPropagation();
	  },
	  callPolice:function(){
		
		  
		this.$http.post('/api/czlpgl/dataservice/woyaobaojing.jsp',{
			work:{
				yhid:1,
				yhmc:1,
				dwid:1,
				fssj:this.alarmTime,
				lxdh:this.userPhone,
				bjdd:this.alarmAddress,
				bjnr:this.alarmInfo
			}
		})
		  .then(function (response) {
			console.log(response.data);
			console.log("yes");
		  })
		  .catch(function (error) {
			console.log(error);
			console.log("no");
		});
		// let that = this;
// 		jsonp('https://api.douban.com/v2/movie/top250?count=20', {
// 			work:{
// 				yhid:1,
// 				yhmc:1,
// 				dwid:1,
// 				fssj:this.alarmTime,
// 				lxdh:this.userPhone,
// 				bjdd:this.alarmAddress,
// 				bjnr:this.alarmInfo
// 			}
// 		}, function (err, data) {
// 		if (err) {
// 		console.log('no');
// 		} else {
// 		console.log('yes');
// 		}
// 		});
	
// 		  username:"匿名",
// 		  userPhone:"13245457878",
// 		  userArea:"钢东社区",
// 		  alarmTime:"15",
// 		  alarmAddress:"请输入详细地址",
// 		  alarmInfo:"请输入报警内容",
	  },
	  getMedia:function(){}
	  
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.Woyaobaojing{
	position:relative;
}
body,html{
	width:100%;
	height:100%;
}
.tjbjContain{
	width:100%;
	
}
.formBox{
	background:#fff;
	padding:0.4rem 0.4rem 1.5rem;
	line-height:40px;
	
}
.formgroup{
	border-bottom:0.01rem solid #d5d5d5;
	overflow: hidden;;
}
.formgroup:last-child{
	border-bottom:0;
}
.formgroup>label{
	display:inline-block;
	width:20%;
	float:left;
}
.formgroup>input{
	border:0;
	display:inline-block;
	line-height: 37px;
    width: 80%;
    float: right;
	font-size:0.42rem;
	padding-left:10px;
	outline:0;
}
.timeBox>input{
	width:50%;
}
.timeBox>div{
	width:75%;
	float:right;
}
.timeBox>div input{
	width:49%;
	font-size:0.42rem;
	float:left;
	border:0;
	height: 1rem;
	line-height: 37px;
}
.submitBox{
	width:100%;
	padding:0 0.4rem;
	box-sizing: border-box;
}
.submitBaojing{
	width:100%;
	text-align: center;
	background:#3fa4e8;
	border:0;
	color:#fff;
	height:1.1rem;
	border-radius:4px;
	
}
input[type='date']{
	background:#fff;
}
.sczjButton img{
	width:1.27rem;
	margin: 0.5rem 0 0.2rem;
}
.camerapic{
	width:0.7rem;;
	float:right;
	margin-top: 0.2rem;
}

.mask{
	width:100%;
	height:100%;
	background:rgba(0,0,0,0.2);
	position:absolute;
	left:0;
	top:0;
}
.maskInfo{
	padding:0.7rem 0.4rem;
	position:absolute;
	bottom:0;
	left:0;
	overflow:hidden;
	background:#fff;
	width: 100%;
	box-sizing: border-box;
}
.maskInfo li{
	width:33.3333%;
	float:left;
	text-align:center;
}
.maskInfo li img{
	width:1.25rem;
	display:block;
	margin:0 auto;
}
.maskInfo li div{
	margin-top:0.3rem;
}
.ivu-select{
	width:80%;
	float:right;
}
.baojinginfo{
	width:80%;
	float:right;
	padding:0 10px;
	box-sizing:border-box;
	line-height: 30px;
	height: 3rem;
    overflow-y: scroll;
	outline:0;
	border:0;
}
</style>



