<template>
  <div class="Test">
    Test
    <button @click = 'getShext'>摄像头</button>
    <div class = "moniSelect">
      <input type="text">
      <ul>
        <li v-for = 'item in optionItem' :key = "item.id" @click = 'clickOption'>{{item.optionInfo}}</li>
      </ul>
    </div>
    
  </div>
</template>

<script>
export default {
  name: 'Test',
  data () {
    return {
      msg: '',
      optionItem:[
        {
          id:1,
          optionInfo:'单位一'
        },
        {
          id:2,
          optionInfo:'单位2'
        },
        {
          id:3,
          optionInfo:'单位3'
        },
      ]
    }
  },
  methods:{
      getShext:function(){
          console.log(1);
          wx.config({
            debug: true, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
            appId: 'wx12fb831e0165e2df', // 必填，公众号的唯一标识
            timestamp: 1555029865, // 必填，生成签名的时间戳
            nonceStr: '1650888638', // 必填，生成签名的随机串
            signature: 'de368033d6385647e8c150eab47612ff19fa1d15',// 必填，签名
            jsApiList: ['chooseImage'] // 必填，需要使用的JS接口列表
          });
          wx.ready(function(){
            console.log("成功验证");
              // config信息验证后会执行ready方法，所有接口调用都必须在config接口获得结果之后，config是一个客户端的异步操作，所以如果需要在页面加载时就调用相关接口，则须把相关接口放在ready函数中调用来确保正确执行。对于用户触发时才调用的接口，则可以直接调用，不需要放在ready函数中。
          });
          wx.checkJsApi({
              jsApiList: ['chooseImage'], // 需要检测的JS接口列表，所有JS接口列表见附录2,
              success: function(res) {
                  console.log(res);
              // 以键值对的形式返回，可用的api值true，不可用为false
              // 如：{"checkResult":{"chooseImage":true},"errMsg":"checkJsApi:ok"}
              }
          });
          wx.chooseImage({
            count: 1, // 默认9
            sizeType: ['original', 'compressed'], // 可以指定是原图还是压缩图，默认二者都有
            sourceType: ['album', 'camera'], // 可以指定来源是相册还是相机，默认二者都有
            success: function (res) {
              var localIds = res.localIds; // 返回选定照片的本地ID列表，localId可以作为img标签的src属性显示图片
              console.log(localIds);
            }
          });
        console.log(12);
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



