<template>
  <div class="Jingqingfenxi">
    <div style = "overflow:hidden">
      <ul class = "timebox">
          <li :class = "{ active: item.isActive }" v-for = "item in timeList" v-bind:key = 'item.id' @click = 'clickTimeCate(item.id)'>{{item.timecate}}</li>
      </ul>
    </div>
     
     <div class="jqcategory">
			  <div class="jqcategorytitle">警情分析</div>
				<div class="bingtubox">
					<div id="myChartOne" style=""></div>
				</div>
        
     </div>
     <div class="chuliqk">
				<div class="jqcategorytitle">处理情况</div>
				<div class="progressBox">
					<Progress :percent="progressNum" status="active" />
					<div class = "progressInfo"><span class= "ywc">已完成{{progressNum}}%</span><span class = "wwc">未完成{{100-progressNum}}%</span></div>
					
				</div>
     </div>
  </div>
</template>

<script>
var echarts = require('echarts')



export default {
  name: 'Jingqingfenxi',
  data () {
    return {
      msg: '',
			progressNum:55,
			
			timeList:[
				{
					id:0,
					timecate:'日',
					isActive:false,
					
				},
				{
					id:1,
					timecate:'周',
					isActive:true,
				},
				{
					id:2,
					timecate:'月',
					isActive:false,
				},
				{
					id:3,
					timecate:'季',
					isActive:false,
				},
			]
    }
  },
  mounted() {
    this.drawLine();
  },
  methods: {
    drawLine() {
      // 基于准备好的dom，初始化echarts实例
      let myChart = echarts.init(document.getElementById('myChartOne'))
      // 绘制图表
      var option = {
        
          // title: { text: 'ECharts 入门示例' },
          tooltip: {
              trigger: 'item',
              formatter: "{a} <br/>{b}: {c} ({d}%)"
          },
         
          series: [
          
              {
                  name:'',
                  type:'pie',
                  radius: ['40%', '55%'],
									center: ['50%', '50%'],
                  label: {
                      normal: {
                          formatter: '{a|{a}}{abg|}\n{hr|}\n{b|{b}:}{c}个 {per|{d}%}  ',
                         
                          rich: {
                              
                              b: {
                                  fontSize: 14,
                                  lineHeight: 33
                              },
                              per: {
                                  color: '#3fa4e8',
                                
                              }
                          }
                      }
                  },
                  data:[
                      {value:20, name:'家庭'},
                      {value:10, name:'经济'},
                      {value:30, name:'劳资'},
                      {value:40, name:'民事'},
                  ]
              }
          ]
          
        
      };
      myChart.setOption(option);
    },
		clickTimeCate:function(currentNum){
			for(var i=0;i<this.timeList.length;i++){
				this.timeList[i].isActive = false;
			}
			this.timeList[currentNum].isActive = true;
		}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.timebox li{
  float:left;
  width:0.9rem;
  height:0.9rem;
  border:1px solid #3fa4e8;
  text-align:center;
  color:#3fa4e8;
  line-height:0.9rem;
  font-size:0.44rem;
  border-radius:50%;
  margin-left:0.6rem;
}
.timebox .active{
	background:#3fa4e8;
	color:#fff;
}
.timebox{
  float:right;
  overflow: hidden;
  margin: 0.2rem 0;
  padding:0 0.4rem;
}
.jqcategory,.chuliqk{
  padding:0.5rem 0.4rem;
  background:#fff;
}
.chuliqk{
	margin-top:0.3rem;
}
.Jingqingfenxi{
	font-size:0.46rem;
	color:#3fa4e8;
}
.bingtubox{
	width:100%;
	height:4.5rem;
}
#myChartOne{
	width:100%;
	height:100%;
}
.progressInfo>span{
	display:inline-block;
	width:50%;
	font-size:0.14rem;
	
}
.progressInfo>span:last-child{
	text-align:right;
}
</style>



