<template>
  <div class="Jingqingpanduan">
    <div class="tjbjContain">
        <div class="formBox">
            <form action="">
                <div class="formgroup">
                    <label for="">姓名</label>
                    <input type="text" value = "" readonly="readonly"/>
                </div>
                <div class="formgroup">
                    <label for="">联系电话</label>
                    <input type="text" readonly="readonly" value = ""/>
                </div>
                <div class="formgroup">
                    <label for="">发生时间</label>
                    <input readonly class="fashengtime" type="text" id="date4" placeholder="">
                    
                </div>
                <div class="formgroup">
                    <label for="">事件地点</label>
                    <input type="text" placeholder = "请输入详细地址"/>
                </div>
                <div class="formgroup">
                    <label for="">报警内容</label>
                    <input type="text" placeholder = "请输入报警内容"/>
                </div>
            </form>
            <ul class = "jqpdPic">
                <li><img src = "../assets/img/zhengzaizhixing.png"></li>
                <li><img src = "../assets/img/zhengzaizhixing.png"></li>
                <li><img src = "../assets/img/zhengzaizhixing.png"></li>
            </ul>
        </div>
        <div class="submitBox">
            <div><input  class = "submitZpdw" type="button" value = "指派单位"/></div>
            <div><input @click = 'liantiaoFun' class = "submitLiantiao" type="button" value = "联调解决"/></div>
            <div><input @click = 'neibuFun' class = "submitNeibu" type="button" value = "内部解决"/></div>
        </div>
    </div>
	
  </div>
</template>

<script>
import selectDate from '../assets/js/selectDate'
export default {
  name: 'Jingqingpanduan',
  data () {
    return {
      msg: '',
      urlid:''
    }
  },
  mounted: function () {
       let h = window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight;
    
	   $(".mask").css("height",h+'px');
       var marginTopNum = h-$(".formBox").height()-$(".submitBox").height();
	   console.log(marginTopNum);
        marginTopNum = marginTopNum/100+3;
        $(".submitBox").css("marginTop",marginTopNum+'rem');
       
        var str = window.location.href;
        this.urlid = str.charAt(str.length-1);
     
      /*数据渲染 */

			
			
			
            
  },
  methods:{
      liantiaoFun:function(){
          console.log("联调");
          console.log(this.urlid);
          this.$router.push({name:'Liantiaojiejue',params:{id:this.urlid}});
      },
      neibuFun:function(){
          console.log("内部");
          this.$router.push({name:'Neibujiejue',params:{id:this.urlid}});
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.Jingqingpanduan{
	position:relative;
}
body,html{
	width:100%;
	height:100%;
}
.tjbjContain{
	width:100%;
	
}
.formBox{
	background:#fff;
	padding:0.4rem 0.4rem;
	line-height:40px;
	
}
.formgroup{
	border-bottom:0.01rem solid #d5d5d5;
	overflow: hidden;;
}
.formgroup:last-child{
	border-bottom:0;
}
.formgroup>label{
	display:inline-block;
	width:20%;
	float:left;
}
.formgroup>input{
	border:0;
	display:inline-block;
	line-height: 37px;
    width: 75%;
    float: right;
	font-size:0.42rem;
	padding-left:10px;
}
.timeBox>input{
	width:50%;
}
.timeBox>div{
	width:75%;
	float:right;
}
.timeBox>div input{
	width:49%;
	font-size:0.42rem;
	float:left;
	border:0;
	height: 1rem;
	line-height: 37px;
}
.submitBox{
	width:100%;
	padding:0 0.4rem;
	box-sizing: border-box;
    display:flex;
    background: #fff;
    padding: 10px 0;
}
.submitBox>div{
    flex:1;
}
.submitLiantiao,.submitNeibu,.submitZpdw{
	width:80%;
    display: block;
    margin: 0 auto;
	text-align: center;
	border:0;
	color:#fff;
	height:1.1rem;
	border-radius:4px;
	
}
.submitNeibu{
    background:#f47272;
}
.submitLiantiao{
    background:#faa345;
}
.submitZpdw{
    background:#3fa4e8;
}
input[type='date']{
	background:#fff;
}
.sczjButton img{
	width:1.27rem;
	margin: 0.5rem 0 0.2rem;
}

.jqpdPic{
    display:flex;
    margin-top: 0.3rem;
}
.jqpdPic li{
    flex:1;
    margin-right:5px;
}
.jqpdPic li:last-child{
    margin-right:0;
}
.jqpdPic li img{
    width:100%;
}
</style>



