import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'
import Jingwu from '@/components/Jingwu'
import Person from '@/components/Person'
import Woyaobaojing from '@/components/Woyaobaojing'
import Liantiaodanwei from '@/components/Liantiaodanwei'
import Neiburenyuan from '@/components/Neiburenyuan'
import Zhengzaizhixing from '@/components/Zhengzaizhixing'
import Yiwancheng from '@/components/Yiwancheng'
import Qunzhong from '@/components/Qunzhong'
import Neibujiejue from '@/components/Neibujiejue'
import Jingqingpanduan from '@/components/Jingqingpanduan'
import Liantiaojiejue from '@/components/Liantiaojiejue'
import Liantiaolistitem from '@/components/Liantiaolistitem'
import Test from '@/components/Test'
import Wenshu from '@/components/Wenshu'
import Newtest from '@/components/Newtest'
import Jingqingfenxi from '@/components/Jingqingfenxi'
import Liantiaoyuan from '@/components/Liantiaoyuan'
import Neibulistitem from '@/components/Neibulistitem'

Vue.use(Router)

export default new Router({
  // mode: 'history',
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index
    },
    
    {
      path: '/Jingwu',
      name: 'Jingwu',
      component: Jingwu,
      children: [  //这里就是二级路由的配置
        {
          path: '/Jingwu/Zhengzaizhixing',
          name: 'Zhengzaizhixing',
          component: Zhengzaizhixing
        },
        {
          path: '/Jingwu/yiwancheng',
          name: 'Yiwancheng',
          component: Yiwancheng
        },
        
      ]
    },
    {
      path: '/Jingqingpanduan/:id',
      name: 'Jingqingpanduan',
      component: Jingqingpanduan
    },
    {
      path: '/person',
      name: 'Person',
      component: Person
    },
    {
      path: '/Woyaobaojing',
      name: 'Woyaobaojing',
      component: Woyaobaojing
    },
    {
      path: '/Liantiaodanwei',
      name: 'Liantiaodanwei',
      component: Liantiaodanwei
    },
    {
      path: '/Neiburenyuan',
      name: 'Neiburenyuan',
      component: Neiburenyuan
    },
  
    {
      path: '/Qunzhong',
      name: 'Qunzhong',
      component: Qunzhong
    },
  
    {
      path: '/Liantiaojiejue',
      name: 'Liantiaojiejue',
      component: Liantiaojiejue
    },
    {
      path: '/Liantiaolistitem',
      name: 'Liantiaolistitem',
      component: Liantiaolistitem
    },
    {
      path: '/Neibulistitem',
      name: 'Neibulistitem',
      component: Neibulistitem
    },
    {
      path: '/Neibujiejue',
      name: 'Neibujiejue',
      component: Neibujiejue
    },
    {
      path: '/Test',
      name: 'Test',
      component: Test
    },
    {
      path: '/Wenshu',
      name: 'Wenshu',
      component: Wenshu
    },
    {
      path: '/Newtest',
      name: 'Newtest',
      component: Newtest
    },
    {
      path: '/Jingqingfenxi',
      name: 'Jingqingfenxi',
      component: Jingqingfenxi
    },
		{
		  path: '/Liantiaoyuan/:type',
		  name: 'Liantiaoyuan',
		  component: Liantiaoyuan
		}
  ]
})



