<template>
  <div class="Liantiaodanwei">
      <div class="liantiaoBox">
        <ul>
            <li class = "firstMunu">
                <img class = "rightArrow" src = "../assets/img/arrowright.png">
                <div>法院、司法所（一般民事纠纷、经济纠纷等）</div>
                <ul class = "picBox">
                    <li>
                        <img src="../assets/img/banner.png" alt="" />
                        <div><span class = "names">张晓磊</span><span class = "phones">13834599306</span></div>
                    </li>
                    <li>
                        <img src="../assets/img/banner.png" alt="" />
                        <div><span class = "names">张晓磊</span><span class = "phones">13834599306</span></div>
                    </li>
                </ul>
            </li>
            <li class = "firstMunu">
                <img class = "rightArrow" src = "../assets/img/arrowright.png">
                <div>社保局（劳资纠纷）</div>
                <ul class = "picBox">
                    <li>
                        <img src="../assets/img/banner.png" alt="" />
                        <div><span class = "names">张晓磊</span><span class = "phones">13834599306</span></div>
                    </li>
                    <li>
                        <img src="../assets/img/banner.png" alt="" />
                        <div><span class = "names">张晓磊</span><span class = "phones">13834599306</span></div>
                    </li>
                </ul>
            </li>
            <li class = "firstMunu">
                <img class = "rightArrow" src = "../assets/img/arrowright.png">
                <div>民政局（婚姻纠纷）</div>
            </li>
            <li class = "firstMunu">
                <img class = "rightArrow" src = "../assets/img/arrowright.png">
                <div>国土住建局（拆迁纠纷等）</div>
            </li>
            
            
            <li class = "firstMunu">
                <img class = "rightArrow" src = "../assets/img/arrowright.png">
                <div>消协（购物退换货纠纷、产品质量问题纠纷）</div>
            </li>
            <li class = "firstMunu">
                <img class = "rightArrow" src = "../assets/img/arrowright.png">
                <div>教育局</div>
            </li>
            <li class = "firstMunu">
                <img class = "rightArrow" src = "../assets/img/arrowright.png">
                <div>交警（交通事故）</div>
            </li>
            
        </ul>
        
    </div>
    <!-- <div class="addCompany">
        <div><img src = "../assets/img/addqunzhong.png">添加单位</div>
    </div> -->
  </div>
</template>

<script>
export default {
  name: 'Liantiaodanwei',
  data () {
    return {
      msg: ''
    }
  },
  mounted:function(){
        $(".firstMunu>div").on("click", function() {
            $(this).parent(".firstMunu").children(".rightArrow").toggleClass("rightArrowRotate");
            $(this).next().slideToggle();
            $(this).parent(".firstMunu").toggleClass("cur");
            $(this).parent(".firstMunu").siblings().removeClass("cur");
            $(this).parent(".firstMunu").siblings().children(".rightArrow").removeClass("rightArrowRotate");
            $(this).parent(".firstMunu").siblings().find(".picBox").slideUp();
        })
  }
 
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.liantiaoBox{
	width:100%;
}
.liantiaoBox ul{
	overflow: hidden;
}
.liantiaoBox ul>li,.addCompany{
	/* height:1.4rem; */
    line-height:1.4rem;
	background:#fff;
	padding:0 0.4rem;
	margin-bottom:0.15rem;
}
.liantiaoBox ul li span{
	float:right;
}
.addCompany{
	text-align: center;
}
.picBox{
	width:100%;
	display:none;
}
.liantiaoBox .picBox>li{
	border-top:1px solid #d1d1d1;
	margin-bottom:0;
}
.picBox>li{
	overflow: hidden;
}
.picBox>li img{
	width:1.44rem;
	height:1.44rem;
	margin-top:0.35rem;
	border-radius:50%;
	float:left;
}
.picBox>li div{
	float:left;
	margin-left:0.74rem;
}
.picBox>li div .names,.picBox>li div .phones{
	line-height:2.14rem;
	float:left;
}
.picBox>li div .phones{
	color:#0086e1;
	margin-left:0.44rem;
}
.rightArrow{
    width:0.35rem;
    float: right;
    margin: 0.4rem 0.2rem 0 0;
    transform: rotate(90deg);
}
.rightArrowRotate{
    transform: rotate(270deg);
}
.addCompany img{
    width:0.45rem;
    color:#333;
    margin-right: 0.2rem;
    position: relative;
    top: 0.05rem;
}

</style>



